<template>
  <div>
    <form @submit.prevent="addTodo">
      <input type="text" v-model="title" name="title" placeholder="Add Todo...">
      <input type="submit" value="Submit" class="btn">
    </form>
  </div>
</template>

<script>
import uuid from 'uuid';
export default {
  name: "AddTodo",
  data() {
    return {
      title: ''
    }
  },
  methods: {
    addTodo() {
      const newTodo = {
        id: uuid.v4(),
        title: this.title,
        completed: false
      }
      // Send up to parent
      this.$emit('add-todo', newTodo);
      // Submit reset
      this.title = '';
    }
  }
}
</script>

<style scoped>

  form {
    display: flex;
    width: 55%;
    margin: auto;
  }

  input[type="text"] {
    font-family: 'Play', sans-serif;
    flex: 10;
    padding: 10px;
  }

  input[type="submit"] {
    font-family: 'Play', sans-serif;
    flex: 2;
  }
</style>


